.app-header {
  display: grid;
  height: 64px;
  position: sticky;
  border-bottom: 0;
  z-index: 5;
  align-items: center;
  background: linear-gradient(var(--primary-color), var(--primary-color));
  transition: background-color 0.3s ease;

  @media (max-width: $mobile-breakpoint) {
    position: relative;
    z-index: 0;
    border-bottom: none;
  }
  .btn-login-signup-wrapper {
    margin-right: 1em;
  }

  &.app-header > div {
    justify-content: space-between;
    align-items: center;
    margin-left: 1.3em;
    .btn-login-sighnup-wrapper {
      margin-right: 28px;
    }
  }

  & nav {
    gap: 9px;
    align-items: center;
    justify-content: space-between;
  }

  .btn-go-back,
  .btn-go-next {
    background-color: rgba(0, 0, 0, 0.427);
    border: none;
    height: 32px;
    width: 32px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
  }
  .btn-login-signup-wrapper {
    margin-right: 1em;
  }
  .btn-go-back svg,
  .btn-go-next svg {
    fill: #ffffff;
  }

  .btn-login {
    font-family: CircularBlack;
    background-color: $clr1;
    color: $bgclr1;
    font-weight: 400;
    height: 39px;
    width: 86px;
    margin: 0.11em;
    border-radius: 500px;
    border: none;
    cursor: pointer;
  }
  .btn-signup {
    font-family: CircularBlack;
    background-color: transparent;
    border: none;

    color: $clr2;
    font-weight: 400;
    height: 39px;
    width: 86px;
    margin: 0.11em;
    &:hover {
      color: $clr1;
      cursor: pointer;
    }
 
  }
  .btn-login,
  .btn-signup {
    margin-top: 0.3em;
  }
  .user-image-container {
    background-color: rgba(0, 0, 0, 0.427);
    border: none;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    margin-top: 1px;
    cursor: pointer;

    img {
      margin: 3px 0 0px 3px;
      border-radius: 50%;
      position: relative;
      height: 24px;
      width: 24px;
    }
  }
}
